import styled from "styled-components";

export const Container = styled.section`
    width: 1200px;
  height:100vh;
  overflow:hidden;
  margin:auto;
  
  
  @media (max-width:576px){
width:100%;
    overflow:auto;
  }
  @media (max-width:900px){
    width: 90%;
  }
  @media (max-width:1300px){
    width: 85%;
  }
  /* @media (min-width:1300px){
 
  } */
`;
