import { DeleteOutlined, UserAddOutlined, UserOutlined } from "@ant-design/icons";
import { Avatar, Card, Divider, Image } from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useTheme } from "styled-components";
import { setFollowers, setFollowing, setUser } from "../../Auth/reducer/AuthReducer";

const FriendsList = ({ userData, token, followers, following }) => {
  const theme = useTheme();
  const dispatch = useDispatch();
 
  const [FollowersList,setFollowersList] = useState([])
  const [FollowingList,setFollowingList] = useState([])
  const BASE_URL = process.env.REACT_APP_BASE_URL;
// console.log(followers)
// console.log(following)
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(false);
    fetchfollowers();
    fetchfollowing();
    setLoading(true);
    setFollowersList(followers)
    setFollowingList(following)
  }, [userData]);
  const addFriend = async (key) => {
  
    
    const response = await axios.patch(
      `${BASE_URL}user/${userData?._id}/${key?.id}`,
      {},

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
          friendId: key?.id,
        },
      }
    );

    dispatch(setUser(response.data))
    dispatch(setFollowing(response.data.following))
    dispatch(setFollowers(response.data.followers))
   
  };
  const fetchfollowers = async () => {
    const response = await axios.get(
      `${BASE_URL}user/${userData._id}/Followers`,

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
        },
      }
    );

    dispatch(setFollowers(response.data));
  };
  const fetchfollowing = async () => {
    const response = await axios.get(
      `${BASE_URL}user/${userData._id}/following`,

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
        },
      }
    );

    dispatch(setFollowing(response.data));
  };
  return (
    <Card style={{ background: theme.light, color: theme.darkSecondary }}>
      <p style={{ textAlign: "center" }}>Following</p>
      <Divider style={{ borderColor: theme.darkSecondary }} type="horizontal" />
      {loading ? (
        following?.length > 0 ? (
         
          following?.map((following, i) => {
            return (
              <div
                key={i}
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-between",
                  margin: "10px",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    gap: "20px",
                  }}
                >
                  <div>
                    <Avatar
                      style={{ background: theme.dark }}
                      size={48}
                      icon={
                        following.picturePath === "" ? (
                          <UserOutlined />
                        ) : (
                          <Image preview={false} src={following.picturePath} />
                        )
                      }
                    />
                  </div>
                  <div>
                    <h4>{following?.firstName + " " + following?.lastName}</h4>
                    <p style={{ fontSize: "0.50rem" }}>
                      {following?.occupation}
                    </p>
                  </div>
                </div>
                <div>
                  <Avatar onClick={()=>addFriend({id:following._id})} style={{background:theme.danger}} icon={<DeleteOutlined />} />
                </div>
              </div>
            );
          })
        ) : (
          <p>No Following</p>
        )
      ) : (
        <div>Loading ...</div>
      )}
      {following?.length > 2 ? (
        <div style={{ textAlign: "center" }}>
          <Link to={`/${userData._id}/friends`}>See All</Link>
        </div>
      ) : (
        ""
      )}
      {/* followers */}
      <p style={{ textAlign: "center" }}>Followers</p>
      <Divider style={{ borderColor: theme.darkSecondary }} type="horizontal" />
      {loading ? (
        followers?.length > 0 ? (
         
          followers?.map((followers, i) => {
            return (
              <div
                key={i}
                style={{
                  display: "flex",
                  alignItems: "center",
                  gap: "20px",
                  margin: "10px",
                }}
              >
                <div>
                  <Avatar
                    style={{ background: theme.dark }}
                    size={48}
                    icon={
                      followers?.picturePath === "" ? (
                        <UserOutlined />
                      ) : (
                        <Image preview={false} src={followers.picturePath} />
                      )
                    }
                  />
                </div>
                <div>
                  <h4>{followers?.firstName + " " + followers?.lastName}</h4>
                  <p style={{ fontSize: "0.50rem" }}>{followers?.occupation}</p>
                </div>
              </div>
            );
          })
        ) : (
          <p>No Followers</p>
        )
      ) : (
        <div>Loading ...</div>
      )}
      {followers?.length > 2 ? (
        <div style={{ textAlign: "center" }}>
          <Link to={`/${userData._id}/friends`}>See All</Link>
        </div>
      ) : (
        ""
      )}
    </Card>
  );
};

export default FriendsList;
