import {
  DeleteOutlined,
  HeartFilled,
  HeartOutlined,
  MessageOutlined,
  UserAddOutlined,
  UserDeleteOutlined,
  UserOutlined,
} from "@ant-design/icons";
import {
  Avatar,
  Button,
  Card,
  Divider,
  Drawer,
  Form,
  Image,
  Input,
  List,
  Modal,
  Popover,
  Typography,
} from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useTheme } from "styled-components";
import { setFollowers, setFollowing, setPost, setPosts, setUser } from "../../Auth/reducer/AuthReducer";
import { useDispatch } from "react-redux";

const PostCard = ({ data, token, userData }) => {
  const BASE_URL = process.env.REACT_APP_BASE_URL;
  const theme = useTheme();

  const [likes, setlikes] = useState(0);
  const [comment, setComment] = useState(0);
  const [comments, setComments] = useState("");
  const [liked, setLiked] = useState(false);
  const [open, setOpen] = useState(false);
  const [form] = Form.useForm();
  const dispatch = useDispatch();


  const [isModalOpen, setIsModalOpen] = useState(false);
  const showModal = () => {
    setIsModalOpen(true);
  };
  const handleOk = () => {
    setIsModalOpen(false);
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };
useEffect(()=>{
  setComment(data?.comments?.length )
  setlikes(Object.keys(data?.likes)?.length)
  setLiked(userData._id in data.likes)
},[data])
  const onFinish = async (values) => {
    
    const response = await axios.post(
      `${BASE_URL}posts/${data._id}/comment`,
      { comment: values.comment, userId: userData._id },

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: data._id,
        },
      }
    );
   
    dispatch(setPost(response.data));
    setComment(response.data.comments.length);
    form.resetFields();
   
  };
  const showDrawer = () => {
    setOpen(true);
  };
  const onClose = () => {
    setOpen(false);
  };

  const addFriend = async () => {
    
    const response = await axios.patch(
      `${BASE_URL}user/${userData?._id}/${data?.userId}`,
      {},

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
          friendId: data.userId,
        },
      }
    );
 
    dispatch(setUser(response.data))
    dispatch(setFollowing(response.data.following))
    dispatch(setFollowers(response.data.follwers))
   
  };
  const likePost = async () => {
    const response = await axios.patch(
      `${BASE_URL}posts/${data._id}/like`,
      { userId: userData._id },
      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: data._id,
        },
      }
    );

    setlikes(Object.keys(response.data.likes).length);
   
    setLiked(userData._id in response.data.likes);
  };
  const deletePost =async()=>{
    const response = await axios.patch(
      `${BASE_URL}posts/delete/${data._id}`,
      {userId:userData._id},
      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: data._id,
        },
      }
    );

    dispatch(setPosts(response.data))
  }

  const handleDelete = async (key) => {

    const response = await axios.patch(
      `${BASE_URL}posts/${data._id}/comment`,
      { comment: key.comment, userId: key.userId },

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: data._id,
        },
      }
    );

    dispatch(setPost(response.data));
    const datas = response.data
   
    setComment(datas.comments.length )
  };
  return (
    <Card style={{ background: theme.light, color: theme.dark }}>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",

          alignItems: "center",
        }}
      >
        <div style={{ display: "flex", alignItems: "center", gap: "20px" }}>
          <Avatar
            size={32}
            style={{background:theme.dark}}
            icon={
              data?.userPicturePath === "" ? (
                <UserOutlined />
              ) : (
                <Image preview={false} src={data?.userPicturePath} />
              )
            }
          />
          <div>
            <h5>{data?.firstName + " " + data?.lastName}</h5>
            <p style={{ fontSize: "0.50rem" }}>
              {data.location ? data.location : "location"}
            </p>
          </div>
        </div>
        {data?.userId === userData?._id ? (
            <Avatar
          
            size={32}
            style={{ background: theme.danger }}
            icon={<DeleteOutlined />}
            onClick={deletePost}
          />
        ) : userData?.following?.includes(data?.userId) ? (
          <Avatar
          
            size={32}
            style={{ background: theme.primary }}
            icon={<UserDeleteOutlined />}
            onClick={addFriend}
          />
        ) : (
          <Avatar
            size={32}
            style={{ background: theme.primary }}
            icon={<UserAddOutlined />}
            onClick={addFriend}
          />
        )}
      </div>
      <div style={{ padding: "10px", fontSize: "0.75rem" }}>
        {data?.description}
      </div>
      <div>
        <Image
          style={{ borderRadius: "10px" }}
          src={data?.picturePath}
          preview={false}
        />
      </div>
      <Divider style={{ borderColor: theme.darkSecondary }} type="horizontal" />
      <div
        style={{
          display: "flex",
          alignItems: "flex-start",
          textAlign: "center",
          gap: "20px",
        }}
      >
        <div >
          {liked ? (
            <HeartFilled onClick={likePost} style={{ color: "#FF5D5D" }} />
          ) : (
            <HeartOutlined  onClick={likePost} />
          )}
          <div onClick={showModal}>{likes}&nbsp;likes</div>
        </div>
        <div>
          <MessageOutlined onClick={showDrawer} />

          <Drawer
            title="All comments"
            placement="right"
            onClose={onClose}
            open={open}
            style={{background:theme.light,color:theme.dark}}
          >
            <div
              style={{
                height: "80vh",
                display: "flex",
                alignItems: "center",
                position: "relative",
                overflow: "hidden",
              }}
            >
              <div
                style={{
                  position: "absolute",
                  top: 0,
                  left: 0,
                  right: 0,
                  textAlign: "center",
                }}
              >
                All Comments
                <Divider
                  style={{ borderColor: theme.darkSecondary }}
                  type="horizontal"
                />
              </div>
              <div
                style={{
                  height: "80%",
                  width: "100%",
                  margin: "10px",
                  overflow: "auto",
                }}
              >
                {data.comments &&
                  data.comments.map((props, i) => {
                    return (
                      <div
                        key={i}
                        style={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "space-between",
                          padding: "10px",
                        }}
                      >
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            gap: "20px",
                          }}
                        >
                          <Avatar
                            size={48}
                            style={{background:theme.dark}}
                            icon={
                              props?.picturePath === "" ? (
                                <UserOutlined />
                              ) : (
                                <Image
                                  preview={false}
                                  src={props?.picturePath}
                                />
                              )
                            }
                          />
                          <div>
                            <h4>{props?.firstName + " " + props?.lastName}</h4>
                            <p>{props?.comment}</p>
                          </div>
                        </div>
                        <div>
                          {props?.userId === userData._id ? 
                          <DeleteOutlined onClick={() => handleDelete(props)} />
                          : ''}
                        </div>
                      </div>
                    );
                  })}
              </div>
              <div
                style={{
                  position: "absolute",
                  bottom: 0,
                  left: 0,
                  right: 0,
                  textAlign: "center",
                }}
              >
                <Divider
                  style={{ borderColor: theme.darkSecondary }}
                  type="horizontal"
                />
                <Form
                  form={form}
                  name="horizontal_login"
                  layout="inline"
                  onFinish={onFinish}
                >
                  <Form.Item name="comment">
                    <Input
                      onChange={(e) => setComments(e.target.value)}
                      style={{ borderRadius: "50px" }}
                      placeholder="write comments..."
                    />
                  </Form.Item>
                  <Form.Item>
                    <Button
                      type="primary"
                      disabled={comments === "" ? true : false}
                      shape="round"
                      htmlType="submit"
                    >
                      comment
                    </Button>
                  </Form.Item>
                </Form>
              </div>
            </div>
          </Drawer>

          <div> {comment}&nbsp;comments</div>
        </div>
      </div>


      <Modal title="Likes" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </Modal>
    </Card>
  );
};

export default PostCard;
