import React, { useEffect, useState } from "react";
import NavBarComponent from "../NavBar/NavBar";
import ProfileCard from "./Partials/ProfileCard";
import { Container } from "./style";
import { Card, Col, Row, message } from "antd";
import NewPost from "./Partials/NewPost";
import PostCard from "./Partials/PostCard";
import axios from "axios";
import { setPosts, setUser } from "../Auth/reducer/AuthReducer";
import { useDispatch } from "react-redux";
import FriendsList from "./Partials/FriendsList";
import { useLocation } from "react-router-dom";
const HomePage = ({ theme, setTheme, userData, token, posts,followers,following }) => {
  const [messageApi, contextHolder] = message.useMessage();

  const location = useLocation();
  const state = location?.state;
// console.log(posts,'posts')
// console.log(following)
  const loginMessage  = state?.loginMessage;
  const BASE_URL = process.env.REACT_APP_BASE_URL;
  const [loading, setLoading] = useState(false);
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);
const [AllPosts,setAllPosts] = useState([])
  const [loginSuccess, setLoginSuccess] = useState(loginMessage);
  const dispatch = useDispatch();
  // console.log(loginSuccess, "mess");
  window.addEventListener("resize", () => {
    setScreenWidth(window.innerWidth);
  });
  const successMessage = () => {
    messageApi.open({
      type: "success",
      content: "Login Successful",
      style: {
        marginTop: "60px",
      },
      duration: 5,
    });
  };


  useEffect(() => {
    fetchUser()
  },[])

  useEffect(() => {
    if (loginSuccess === "success") {
      if (state === null) {
        console.log(state, 'null')
      } else {
        successMessage();
        setLoginSuccess("");
        window.history.replaceState({}, document.title);
      }
    }
    fetchPosts();
    // setAllPosts(posts)
    // console.log(AllPosts,'all posts from useeffect')
  }, [userData]);
  useEffect(()=>{
    // fetchPosts();
    setAllPosts(posts)
  },[posts])
  const fetchUser = async () => {
    await axios
    .get(`${BASE_URL}user/${userData._id}`, {
      headers: {
        Authorization: "Bearer " + token,
      },
      params:{
        userId:userData._id
      }
    })
    .then((res) => {
    
      // setPosts(res.data);
      // setLoading(false);
     console.log(res)
      dispatch(setUser(res.data));
     
    })
    .catch((err) => console.log(err));
  }
  const fetchPosts = async () => {
    // setLoading(true);
   await axios
      .get(`${BASE_URL}posts/${userData._id}`, {
        headers: {
          Authorization: "Bearer " + token,
        },
        params:{
          userId:userData._id
        }
      })
      .then((res) => {
      
        // setPosts(res.data);
        // setLoading(false);
       
        dispatch(setPosts(res.data));
       
      })
      .catch((err) => console.log(err));
  };

//  console.log(AllPosts,'all posts')
 

  

  return (
    <>
      {contextHolder}
      {loading ? (<div>Loading....</div>) : (
      <Container>
        <NavBarComponent data={userData} theme={theme} setTheme={setTheme} />
       <div style={{marginTop:60}}>
       <div style={{ margin:'30px 0'}}>
          <Row gutter={[24, 24]}>
            <Col lg={6} sm={10} span={24}>
              <div style={{ height: "90vh", overflow: "auto" }}>
                <ProfileCard data={userData} token={token} />
                {screenWidth < 993 ? (
                  <div style={{ marginTop: "10px" }}>
                      <FriendsList token={token} userData={userData} followers={followers} following={following} />
                  </div>
                ) : (
                  ""
                )}
              </div>
            </Col>

            <Col lg={12} sm={14} span={24}>
              <div style={{ height: "90vh", overflow: "auto" }}>
                <Row gutter={[0, 12]}>
                  <Col span={24}>
                    <NewPost data={userData} token={token} />
                  </Col>

                  {AllPosts && AllPosts?.length > 0 ? (
                    AllPosts?.map((post, i) => {
                      return (
                        <Col span={24} key={i}>
                          <PostCard
                            userData={userData}
                            token={token}
                            data={post}
                          />
                        </Col>
                      );
                    })
                  ) : (
                   <Col span={24}> <Card style={{textAlign:'center'}}>no posts</Card></Col>
                  )}
                </Row>
              </div>
            </Col>
            {screenWidth > 993 ? (
              <Col lg={6} span={24}>
                <div style={{ height: "90vh", overflow: "auto" }}>
                  <FriendsList token={token} userData={userData} followers={followers} following={following} />
                </div>
              </Col>
            ) : (
              ""
            )}
          </Row>
        </div>
       </div>
      </Container>
      )}
    </>
  );
};

export default HomePage;
