import { UserOutlined } from "@ant-design/icons";
import { Avatar, Button, Card, Divider, Form, Image, Input, Row, Upload } from "antd";
import axios from "axios";
import React, { useState } from "react";
import { BsImage } from "react-icons/bs";
import { useDispatch } from "react-redux";
import { useTheme } from "styled-components";
import { setPosts } from "../../Auth/reducer/AuthReducer";

const NewPost = ({data,token}) => {
  const BASE_URL=process.env.REACT_APP_BASE_URL
  const theme = useTheme();
  const [descriptions, setDescription] = useState("");
  const [file, setFile] = useState({});
  const [urls, setUrls] = useState([]);
  const [fileList, setFileList] = useState([]);
const dispatch = useDispatch();
  const postHandler = async() => {
    const userId = data._id;
    const description = descriptions;
    const picturePath = urls;
  
  const AllData = {userId,description,picturePath};


  const response = await axios.post(`${BASE_URL}new_post`,AllData,{
    headers: {
     
        'Authorization': 'Bearer '+token,
    },
    
    
})
 console.log(response.data)
  setDescription("")
  setUrls("")
  setFileList([])
  dispatch(setPosts(response.data))
  
};



  const uploadHandler = async (e) => {
    setFileList(e.fileList);
    setFile(e.file);
    
    const formData = new FormData();

    formData.append("picture", file.originFileObj);

    await fetch(`${BASE_URL}upload`, {
      method: "POST",
      body: formData,
    })
      .then((response) => response.json())
      .then((result) => {
  
        setUrls(result.fileUrls);
      })
      .catch((error) => {
        console.error(error.json());
      });
  };
  return (
    <Card style={{ background: theme.light }}>
      <div style={{paddingBottom:'20px',color:theme.primary}}>Hey {data.firstName+' '+data.lastName},</div>
      <div style={{ display: "flex", gap: "20px", alignItems: "center" }}>
        <div>
          <Avatar style={{background:theme.dark}} size={32} icon={data.picturePath === '' ?  <UserOutlined /> : <Image preview={false} src={data.picturePath}/>} />
        </div>
        <Input
          style={{ borderRadius: "50px", background: theme.secondary }}
          placeholder="write something..."
          value={descriptions}
          onChange={(e) => setDescription(e.target.value)}
        />
      </div>
      <Divider style={{ borderColor: theme.darkSecondary }} type="horizontal" />
      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
        }}
      >
        <Upload
// action={'http://localhost:5000/upload'}
          onChange={uploadHandler}
          listType="picture"
          fileList={fileList}
          maxCount={1}
          onRemove={() => setUrls([])}
        >
          <Button icon={<BsImage />}>
            
            Image
          </Button>
        </Upload>
        <Button disabled={urls.length<=0  } shape="round" type="primary" onClick={postHandler}>
          Post
        </Button>
      </div>
    </Card>
  );
};

export default NewPost;
