import {
  EditOutlined,
  EyeOutlined,
  LinkedinFilled,
  PushpinOutlined,
  TwitterOutlined,
  UserAddOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { Avatar, Button, Card, Divider, Image, Tooltip } from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { BsBriefcase } from "react-icons/bs";
import { GoLocation } from "react-icons/go";
import { useNavigate } from "react-router-dom";
import { useTheme } from "styled-components";

const ProfileCard = ({ data, token }) => {
  const BASE_URL = process.env.REACT_APP_BASE_URL;
  const [userData, setUserData] = useState(data);
  const navigate = useNavigate();
  useEffect(() => {
    fetchUser();
  }, [data]);
  // console.log(userData.followers,'usedata')
  const fetchUser = async () => {
    const response = await axios.get(`${BASE_URL}user/${data._id}`, {
      headers: {
        Authorization: "Bearer " + token,
      },
      params: {
        id: data._id,
      },
    });

    setUserData(response.data);
  };
  const theme = useTheme();

  return (
    <Card style={{ background: theme.light, color: theme.darkSecondary }}>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <div style={{ display: "flex",flexDirection:'column', }}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "column",
            }}
          >
            <Avatar
              style={{ background: theme.dark }}
              size={48}
              icon={
                userData.picturePath === "" ? (
                  <UserOutlined />
                ) : (
                  <Image preview={false} src={userData.picturePath} />
                )
              }
            />
            <h4 style={{ color: theme.primary }}>
              {userData?.firstName}&nbsp;{userData?.lastName}
            </h4>
          </div>
          <div style={{ display: "flex", alignItems: "center", gap: "20px" }}>
            <div style={{ textAlign: "center" }}>
              <p>{userData?.following?.length || 0}</p> <p>following</p>
            </div>
            <div style={{ textAlign: "center" }}>
              <p>{userData?.followers?.length || 0}</p> <p>followers</p>
            </div>
          </div>
        </div>
        <Tooltip placement="bottom" title="view profile">
          <EyeOutlined onClick={() => navigate(`/profile/${userData._id}`)} />
        </Tooltip>
      </div>
      <Divider style={{ borderColor: theme.darkSecondary }} type="horizontal" />
      <div style={{ fontSize: "0.75rem" }}>
        <div style={{ display: "flex", alignItems: "center", gap: "20px" }}>
          <GoLocation />
          {userData.location ? userData.location : "Location Not Added"}
        </div>
        <div style={{ display: "flex", alignItems: "center", gap: "20px" }}>
          <BsBriefcase />
          {userData.occupation ? userData.occupation : "occupation Not Added"}
        </div>
      </div>
      {/* <Divider style={{borderColor:theme.darkSecondary}} type="horizontal" />
      <div style={{fontSize:'0.75rem' }}>
        <div>Profile views : {data?.viewedProfile}</div>
        <div>Impressions : {data?.impressions}</div>
      </div> */}
      <Divider style={{ borderColor: theme.darkSecondary }} type="horizontal" />
      <div>
        <h5>Social Profiles</h5>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <div style={{ display: "flex", alignItems: "center", gap: "20px" }}>
            <TwitterOutlined />
            <div>
              <h6>Twitter</h6>
              <p style={{ fontSize: "0.75rem" }}>Social Network</p>
            </div>
          </div>
          <EditOutlined />
        </div>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <div style={{ display: "flex", alignItems: "center", gap: "20px" }}>
            <LinkedinFilled />
            <div>
              <h6>Linkedin</h6>
              <p style={{ fontSize: "0.75rem" }}>Network</p>
            </div>
          </div>
          <EditOutlined />
        </div>
      </div>
    </Card>
  );
};

export default ProfileCard;
